r=\"id of selectedValues\" :value=\"id\" selected=\"selected\">{{ id }}</option>\n    </select>\n\n    <tree-modal\n      ref=\"modal\"\n      :id=\"id\"\n      :title=\"modalTitle\"\n      :source=\"source\"\n      :value=\"selectedValues\"\n      @selected=\"handleSelected\"\n      v-bind=\"$attrs\"\n      :disabled=\"disabled\"\n      :readonly=\"readonly\"\n      :search-text=\"searchText\"\n    />\n  </div>\n</template>\n\n<script>\nimport { cloneDeep } from 'lodash-es';\nimport { reactive, ref, toRefs, watch } from 'vue';\nimport { computed, provide } from 'vue-demi';\nimport TreeItem from './tree-item';\nimport TreeModal from './tree-modal';\n\nexport default {\n  name: 'modal-tree',\n  components: { TreeModal, TreeItem  },\n  provide() {\n    return {\n      app: this\n    };\n  },\n  data() {\n    return {\n      search: {\n        q: '',\n        enabled: false\n      },\n      selected: {\n        items: [],\n      },\n    }\n  },\n  props: {\n    id: String,\n    name: String,\n    title: String,\n    disabled: null,\n    readonly: null,\n    value: null,\n    source: [String, Object, Array, Function],\n    items: [Array, Function],\n    modalTitle: String,\n    vertical: Boolean,\n    branchSelectable: {\n      type: Boolean,\n      default: false\n    },\n    selectAllChildren: {\n      type: Boolean,\n      default: false\n    },\n    placeholder: {\n      type: String,\n      default: '- No selected -'\n    },\n    multiple: {\n      type: Boolean,\n      default: false\n    },\n    buttonText: {\n      type: String,\n      default: 'Select'\n    },\n    itemClass: {\n      type: String,\n      default: 'badge bg-primary badge-pill'\n    },\n    searchText: {\n      type: String,\n      default: 'Search'\n    }\n  },\n  setup(props) {\n    const state = reactive({\n      search: {\n        q: '',\n        enabled: false\n      },\n      selected: {\n        items: [],\n      },\n      currentValue: props.value\n    });\n    const modal = ref(null);\n\n    if (!Array.isArray(state.currentValue)) {\n      state.currentValue = [state.currentValue];\n    }\n\n    provide('id', props.id);\n    provide('name', props.name);\n    provide('multiple', props.multiple);\n\n    function openSelector() {\n      modal.value.show();\n    }\n\n    function show() {\n      modal.value.show();\n    }\n\n    function hide() {\n      modal.value.hide();\n    }\n\n    function deleteItem(i, item) {\n      state.selected.items = state.selected.items.filter(it => it.id !== item.id);\n    }\n\n    function handleSelected(items) {\n      items = cloneDeep(items);\n      state.selected.items = items;\n    }\n\n    watch(() => props.items, async (v) => {\n      if (typeof v === 'function') {\n        v = await Promise.resolve(v());\n      }\n\n      if (!Array.isArray(v)) {\n        v = [v];\n      }\n\n      state.selected.items = v.filter((item) => {\n        return state.currentValue.indexOf(Number(item.id)) !== -1\n          || state.currentValue.indexOf(item.id) !== -1;\n      });\n    }, { immediate: true });\n\n    const selectedValues = computed(() => {\n      return state.selected.items.map(item => item.id);\n    });\n\n    const selectedItems = computed(() => state.selected.items);\n\n    const canModify = computed(() => {\n      return !props.readonly && !props.disabled;\n    });\n\n    return {\n      ...toRefs(state),\n      modal,\n      selectedValues,\n      selectedItems,\n      canModify,\n\n      openSelector,\n      show,\n      hide,\n      deleteItem,\n      handleSelected,\n    }\n  },\n};\n</script>\n\n<style scoped>\n.c-item {\n  padding-left: .75rem;\n  padding-right: .75rem;\n  padding-top: .5rem;\n  padding-bottom: .5rem;\n}\n</style>\n"],"sourceRoot":""}]);
